const Login = () => (
		<Layout>
			<section>
				<div className="container pt-3">
					<div className="row">
						<div className="col-md-4 offset-md-4">
							<div className="toggle-login">
								<div className="login">
									<h3 className="mb-4">Entrar</h3>
									
									Login
								</div>

								<div className="register" style="display: none;">
									<h3 className="mb-4">Criar conta</h3>
									
									Registro
								</div>
							</div>
						</div>
					</div>
				</div>
			</section>
		</Layout>
	);

export default Login;